@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src:
    url("../fonts/oswaldregular.woff2") format("woff2"),
    url("../fonts/oswaldregular.woff") format("woff");
}

@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 500;
  font-display: swap;
  src:
    url("../fonts/oswaldmedium.woff2") format("woff2"),
    url("../fonts/oswaldmedium.woff") format("woff");
}

:root {
  --accent-font: "Oswald", "Impact", sans-serif;
  --main-font: "Arial", "Helvetica", sans-serif;
}

*,
::before,
::after {
  box-sizing: border-box;
}

img {
  max-width: 100%;
  object-fit: cover;
}

:root {
  --text-color-accent: #000000;
  --text-color-main: #444444;
  --text-color-dark: #222222;
  --text-color-contrast: #ffffff;

  --bg-color-main: #ffffff;
  --bg-color-darken: #f2f2f2;
  --bg-color-dark: #eaeaea;
  --bg-color-accent: #68b738;

  --border-color-light: #e6e6e6;
  --border-color-lighten: #e7e7e7;
  --border-color-dark: #d9d9d9;
  --border-color-darken: #cdcdcd;
}

.page {
  font-family: var(--main-font);
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  color: var(--text-color-main);

  word-wrap: break-word;
  overflow-wrap: break-word;
}

.page__body {
  margin: 0;
}

.page__header {
  background-color: var(--bg-color-main);
}

.page__centerer {
  width: 320px;
  margin: auto;
  padding: 0 20px;
}

.page__hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.button {
  display: block;
  width: fit-content;
  padding: 14px 20px;
  border: none;

  font-family: var(--accent-font);
  font-weight: 400;
  font-size: 20px;
  line-height: 26px;
  color: var(--text-color-contrast);
  text-transform: uppercase;
  text-decoration: none;
  text-align: center;

  background-color: var(--bg-color-accent);
  cursor: pointer;

  &--size_wide {
    width: 100%;
  }

  &--color_gray {
    color: var(--text-color-main);
    background-color: var(--bg-color-darken);
  }
}

.page-title {
  font-family: var(--accent-font);
  color: var(--text-color-accent);
}

.page-title__header {
  margin: 0;

  font-family: inherit;
  font-weight: 400;
  font-size: 36px;
  line-height: 36px;
}

.page-title__text {
  margin: 0;

  font-family: inherit;
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  text-transform: uppercase;
}

.page-title__header + .page-title__text {
  margin-top: 37px;
}

.section-title {
  font-family: var(--accent-font);
  font-weight: 400;
  font-size: 36px;
  line-height: 40px;

  color: var(--text-color-accent);
}

.main-header {
  background-color: var(--bg-color-main);
}

.main-header__nav {
  margin: 0 -20px;
}

.main-nav {
  border-bottom: 1px solid var(--border-color);
  font-family: var(--accent-font);

  background-color: var(--bg-color-main);
}

.main-nav__list {
  margin: 0;
  padding: 0;

  list-style-type: none;
}

.main-nav__item {
  background-color: var(--bg-color-main);
}

.main-nav__link {
  display: block;
  width: 100%;
  padding: 22px 20px;
  border-top: 1px solid var(--border-color-light);

  font-weight: 500;
  font-size: 20px;
  line-height: 20px;
  color: var(--text-color-accent);
  text-align: center;
  text-decoration: none;
  text-transform: uppercase;
}

.logo {
  flex-grow: 1;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  padding: 12px 0 16px;

  text-decoration: none;

  &[aria-current="page"] {
    pointer-events: none;
  }
}

.logo__image {
  margin-right: 56px;
}

.main-footer {
  background-color: var(--bg-color-darken);
}

.social__list {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0;
  padding: 0;

  list-style-type: none;
}

.social__item {
  margin: 15px;
}

.social__link {
  display: block;
  width: 22px;
  height: 22px;

  background-color: var(--bg-color-accent);
}

.social__icon {
  fill: currentColor;
}

.creators {
  color: var(--text-color-main);
  text-decoration: none;
}

.banner {
  background-color: var(--bg-color-main);
}

.banner__container {
  background-image:
    url("../img/bg-index-banner.jpg"),
    linear-gradient(to bottom, var(--bg-color-accent) 0, var(--bg-color-accent) 302px, transparent 302px, transparent 100%);
  background-position: center top, center top;
  background-size: auto, 100% 100%;
  background-repeat: no-repeat;
}

.banner__content {
  font-family: var(--accent-font);
  font-weight: 400;
  text-align: center;
  color: var(--text-color-contrast);
}

.banner__title {
  font-weight: inherit;
  font-size: 36px;
  line-height: 36px;
  color: inherit;
}

.banner__text {
  font-weight: inherit;
  font-size: 14px;
  line-height: 14px;
  text-transform: uppercase;
}

.modules {
  background-color: var(--bg-color-main);
}

.module-card {
  padding: 20px;
  padding-bottom: 0;

  background-color: var(--bg-color-darken);
}

.module-card__title {
  display: flex;
  justify-content: flex-start;
  align-items: center;

  margin: 0 0 22px;
  font-family: var(--accent-font);
  font-weight: 400;
  font-size: 24px;
  line-height: 37px;
  text-transform: uppercase;
  color: var(--text-color-accent);

  &::before {
    content: "";

    flex-shrink: 1;
    display: block;
    width: 50px;
    height: 50px;
    margin-right: 18px;
    border-radius: 50%;

    background-color: var(--bg-color-accent);
  }
}

.module-card__text {
  font-family: var(--main-font);
  color: var(--text-color-main);
}

.module-card__link {
  display: block;
  padding: 15px 0 18px;
  border-top: 1px solid var(--border-color-dark);

  font-family: var(--accent-font);
  font-weight: 400;
  font-size: 16px;
  line-height: 16px;
  text-transform: uppercase;
  text-decoration: none;
  color: var(--text-color-accent);
}

.features {
  background-color: var(--bg-color-main);
}

.features__title {
  margin-bottom: 40px;
}

.features__list {
  margin: 0;
  padding: 0;

  list-style-type: none;
}

.features__item {
  display: flex;
  justify-content: flex-start;
  align-items: center;

  &::before {
    content: "";

    flex-shrink: 0;
    display: block;
    width: 60px;
    height: 60px;
    margin-right: 20px;

    background-color: var(--bg-color-accent);
  }
}

.result {
  background-color: var(--bg-color-dark);
}

.result__title {
  margin-bottom: 40px;
}

.result-metric--bordered {
  position: relative;

  padding: 15px;
  padding-bottom: 18px;
  border: 1px solid var(--border-color-darken);

  .result-metric__name {
    position: absolute;
    top: 100%;
    left: 50%;

    width: 87px;
    padding: 0 10px;

    font-size: 12px;
    line-height: 12px;
    text-align: center;

    background-color: var(--bg-color-dark);
    transform: translate(-50%, -10px);
  }

  .result-metric__count {
    margin: 0;

    font-family: var(--accent-font);
    font-weight: 500;
    font-size: 24px;
    line-height: 24px;
    text-align: center;
    color: var(--text-color-accent);
  }
}

.result-metric--texted {
  font-family: var(--accent-font);
  font-weight: 400;
  font-size: 14px;
  line-height: 20px;
  text-align: center;
  text-transform: uppercase;
  color: var(--text-color-accent);

  .result-metric__name {
    display: inline-block;

    &::after {
      content: ":";

      display: inline-block;
    }
  }

  .result-metric__count {
    display: inline-block;
    margin: 0;
  }
}

.compare__images {
  padding: 0;
}

.compare__image img {
  width: 100%;
  height: auto;
}

.compare-toggler {
  font-family: var(--accetn-font);
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
  color: var(--text-color-accent);
}

.contact__content {
  background-color: var(--bg-color-main);

  &--inner {
    background-color: var(--bg-color-darken);
  }
}

.contact__title {
  font-family: var(--accent-font);
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
  text-transform: uppercase;
  color: var(--text-color-accent);
}

.contact__address {
  font-size: 14px;
  line-height: 20px;
}

.contact__map {
  height: 362px;

  background-color: var(--bg-color-accent);
  background-image: url("../img/bg-map-mobile.jpg");
  background-position: center top;
  background-size: contain;
  background-repeat: no-repeat;
}

.brief {
  background-color: var(--bg-color-main);
}

.brief__notice {
  color: var(--text-color-accent);
  text-align: center;
}

.form__fieldset {
  margin: 0;
  margin-bottom: 34px;
}

.form-fieldset {
  padding: 0;
  border: none;
}

.form-fieldset__legend {
  font-family: var(--accent-font);
  font-weight: 400;
  font-size: 24px;
  line-height: 26px;
  color: var(--text-color-accent);
}

.form-input {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  width: 100%;

  font-family: var(--accent-font);
  font-weight: 400;
  font-size: 16px;
  line-height: 18px;
  text-transform: uppercase;
}

.form-input__label {
  flex-shrink: 0;
  margin-right: 10px;
}

.form-input__field {
  flex-grow: 1;
  padding: 14px;
  border: 2px solid var(--border-color-lighten);

  font: inherit;

  background-color: var(--bg-color-main);

  &::placeholder {
    color: inherit;
  }
}

.form-textarea {
  width: 100%;
}

.form-textarea__field {
  width: 100%;
  height: 150px;
  padding: 14px;
  border: 2px solid var(--border-color-lighten);

  font-family: var(--accent-font);
  font-weight: 400;
  font-size: 16px;
  line-height: 18px;
  text-transform: uppercase;

  background-color: var(--bg-color-main);
  resize: none;

  &::placeholder {
    color: inherit;
  }
}

.form-radio__input {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.form-radio__label {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  width: 100%;

  font-family: var(--accent-font);
  font-weight: 400;
  font-size: 16px;
  line-height: 18px;
  text-transform: uppercase;
  color: var(--text-color-dark);

  &::before {
    content: "";

    flex-shrink: 0;
    display: block;
    width: 30px;
    height: 30px;
    margin-right: 15px;
    border: 1px solid var(--border-color-lighten);

    border-radius: 50%;
  }
}

.form-checkbox__input {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.form-checkbox__label {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  width: 100%;

  font-family: var(--accent-font);
  font-weight: 400;
  font-size: 16px;
  line-height: 18px;
  text-transform: uppercase;
  color: var(--text-color-dark);

  &::before {
    content: "";

    display: block;
    width: 30px;
    height: 30px;
    margin-right: 15px;
    border: 1px solid var(--border-color-lighten);
  }
}

.catalog__list {
  margin: 0;
  border: none;
  border-collapse: collapse;
}

.catalog-list {
  width: 100%;
}

.catalog-list__item {
  margin-bottom: 40px;
}

.catalog-item {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
}

.catalog-item__title {
  font-family: var(--accent-font);
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
  color: var(--text-color-dark);
}

.catalog-item__link {
  color: inherit;
  text-decoration: none;
}

.catalog-item__weight,
.catalog-item__tasty,
.catalog-item__price {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  width: 100%;

  font-size: 12px;
  line-height: 14px;

  &::before {
    margin-right: auto;
    vertical-align: middle;
  }
}

.catalog-item__weight::before {
  content: "Масса";
}

.catalog-item__tasty::before {
  content: "Вкус";
}

.catalog-item__price::before {
  content: "Цена";
}

.catalog-item__order {
  width: 100%;
}

.catalog-all__title {
  font-family: var(--accent-font);
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
  text-align: center;
  text-transform: uppercase;
  color: #111111;
}

.catalog-all__text {
  text-align: center;
}

.additional__list {
  width: 100%;
  border: none;
  border-collapse: collapse;
}

.additional__order-all {
  display: block;
  min-height: 290px;

  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
  color: var(--text-color-contrast);
  text-align: center;
  text-decoration: none;

  background-color: var(--bg-color-accent);
  background-image: url("../img/bg-order-mobile.jpg");
  background-size: auto;
  background-position: center top;
  background-repeat: no-repeat;
}

.additional-item {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
}

.additional-item__title {
  width: 100%;

  font-family: var(--accetn-font);
  font-weight: 400;
  font-size: 16px;
  line-height: 16px;
  color: var(--text-color-dark);
}

.additional-item__count,
.additional-item__price {
  width: 100%;

  line-height: 14px;
}

.additional-item__order {
  width: 100%;
}

.additional-item__button {
  width: 100%;
}
